import {
    DesktopOutlined,
    FileOutlined,
    PieChartOutlined,
    TeamOutlined,
    UserOutlined,
    MenuFoldOutlined,
    MenuUnfoldOutlined,
} from '@ant-design/icons';
import { Layout, Menu ,Button} from 'antd';
import React, { useState } from 'react';
import { Outlet } from 'react-router-dom';

function getItem(label, key, icon, children,path) {
    return {
        key,
        icon,
        children,
        label,
        path,
    };
}

const items = [
   getItem ('Home', '1', <PieChartOutlined />,'','/'),
    getItem('About', '2', <DesktopOutlined />,'','/about'),
    getItem('Skills', 'sub2', <TeamOutlined />,'','/skills' ),
    getItem('Projects', 'sub1', <UserOutlined />,'','/project'),
    getItem('Contact', '9', <FileOutlined />,'','/contact'),
];

const Template = () => {
    const [collapsed, setCollapsed] = useState(false);
    const toggleCollapsed = () => {
        setCollapsed(!collapsed);
      };
  
      const handleclick = (e) => {
        console.log(e.key);
        // Result=items.find((v)=>v.key===e.key);
        // console.log(e.target.getAttribute(items));
        console.log(items);
        const res = items.find((v) => v.key === e.key);
        console.log(res.path);
        //  location.href=res.path;
        window.location.href = res.path;
      };

      
    return (
        <div>
            <Layout className='lay1'>
                <div style={{ width:150 }} className='layrow' >
                    <Button onClick={toggleCollapsed} style={{ marginBottom: 16, backgroundColor:'orangered',border: 0,}} >
                        {collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
                    </Button>
                    <Menu defaultOpenKeys={['sub1']}  defaultSelectedKeys={['1']} inlineCollapsed={collapsed} items={items} onClick={handleclick} className='lay' />
                </div>
                <Layout className="site-layout">
                    <Outlet />
                </Layout>
            </Layout>
        </div>
    );
};

export default Template;